@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

	@media (max-width: 650px){
		padding: 30px 0;
	}

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

		@media (max-width: 890px){
			flex-direction: column;
			gap: 20px;
		}
  }

  &__logo {
    display: flex;

    img {
      margin-right: 15px;
			@media (max-width: 392px){
				margin-left: 30px;
			}
			@media (max-width: 350px){
				margin-left: 10px;
			}
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
