@import './variables';
@import './libs/normalize';

@import './components/all';

body {
  background-color: $background;
}

.wrapper {
  width: calc(100vw - 100px);
  height: 100%;
  background-color: #fff;
  margin: 50px auto;
  border-radius: 10px;
  max-width: 1400px;
	@media (max-width: 450px){
		width: calc(100vw - 40px);
		margin-top: 10px;
	}
}

.content {
  padding: 40px 0;

  &__title {
    margin: 35px 0;
  }

  &__items {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows: repeat(2, 1fr);
		grid-column-gap: 11px;
		@media (max-width: 1500px){
			grid-template-columns: repeat(3, 1fr);
		}

		@media (max-width: 1150px){
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 767px){
			grid-template-columns: repeat(1, 1fr);
		}
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

		@media (max-width: 1320px){
			flex-direction: column;
			.categories {
				margin-bottom: 20px;
			}

			.categories li {
				margin-right: 20px;
			}
		}

		@media (max-width: 1080px){
			.categories {
				width: 750px;
				overflow: scroll;

				ul {
					width: 790px;
				}
			}
		}

		@media (max-width: 920px){
			.categories {
				width: 550px;
			}
		}

		@media (max-width: 700px){
			.categories {
				width: 480px;
			}
		}

		@media (max-width: 630px){
			.categories {
				width: 400px;
			}
		}

		@media (max-width: 560px){
			.categories {
				width: 300px;
			}
		}

		@media (max-width: 430px){
			.categories {
				width: 260px;
			}
		}

		@media (max-width: 390px){
			.categories {
				width: 220px;
			}
		}
  }
}

.container {
  width: $container-width;
  margin: 0 auto;

  &--cart {
    max-width: 820px;
    margin: 90px auto;
    .content__title {
      margin: 0;
    }
  }
}

.cart {
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .content__title {
    display: flex;
    align-items: center;
    font-size: 32px;
		@media (max-width: 500px){
			font-size: 22px;
		}

    svg {
      position: relative;
      top: -2px;
      width: 30px;
      height: 30px;
      margin-right: 10px;
      path {
        stroke: $black;
        stroke-width: 1.9;
      }
    }
  }

  &__clear {
    display: flex;
    align-items: center;
    cursor: pointer;
    @include noselect();

    span {
      display: inline-block;
      margin-left: 7px;
      color: #b6b6b6;
      font-size: 18px;
			@media (max-width: 500px){
				font-size: 14px;
			}
    }

    span,
    svg,
    path {
      transition: all $duration ease-in-out;
    }

    &:hover {
      svg {
        path {
          stroke: darken($color: #b6b6b6, $amount: 50);
        }
      }
      span {
        color: darken($color: #b6b6b6, $amount: 50);
      }
    }
  }

	.content__items {
		display: block;
	}

  &__item {
    display: flex;
		justify-content:space-between;
    width: 100%;
    border-top: 1px solid $gray-line;
    padding-top: 30px;
    margin-top: 30px;

    &-img {
      display: flex;
      align-items: center;
      margin-right: 15px;
      width: 10%;
			@media (max-width: 530px){
				margin-right: 10px;
			}

      img {
        width: 80px;
        height: 80px;
				@media (max-width: 850px){
					width: 60px;
					height: 60px;
				}
				@media (max-width: 640px){
					width: 40px;
					height: 40px;
				}
      }
    }

    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 50%;
			@media (max-width: 600px){
				width: 40%;
			}
			@media (max-width: 530px){
				width: 30%;
			}

      h3 {
        font-weight: bold;
        font-size: 22px;
        line-height: 27px;
        letter-spacing: 0.01em;
				@media (max-width: 850px){
					font-size: 18px;
				}
				@media (max-width: 640px){
					font-size: 14px;
				}
				@media (max-width: 530px){
					font-size: 12px;
					line-height: 14px;
				}
      }

      p {
        font-size: 18px;
        color: #8d8d8d;
				@media (max-width: 850px){
					font-size: 15px;
				}
				@media (max-width: 640px){
					font-size: 13px;
				}
				@media (max-width: 530px){
					font-size: 11px;
					line-height: 13px;
				}
      }
    }

    &-count {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 15%;
			@media (max-width: 600px){
				width: 20%;
			}

      &-minus {
        svg {
          path:first-of-type {
            display: none;
          }
        }
				&:disabled {
					opacity: .4;
					pointer-events: none;
				}
				&:disabled:hover {
					background-color: #fff;
					border-color: $orange;
				}
      }

      b {
        font-size: 22px;
      }
    }

    &-price {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 20%;
			@media (max-width: 600px){
				width: 23%;
			}
			@media (max-width: 400px){
				width: 40%;
			}

      b {
        font-weight: bold;
        font-size: 22px;
        letter-spacing: 0.01em;
				@media (max-width: 850px){
					font-size: 18px;
				}
				@media (max-width: 530px){
					font-size: 14px;
				}
      }
    }

    &-remove {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 3%;

      .button {
        border-color: darken($color: $gray-line, $amount: 10);
      }

      svg {
        transform: rotate(45deg);

        path {
          fill: darken($color: $gray-line, $amount: 15);
        }
      }

      .button {
        svg {
          width: 11px;
          height: 11px;
          position: relative;
        }
        &:hover,
        &:active {
          border-color: darken($color: $gray-line, $amount: 80);
          background-color: darken($color: $gray-line, $amount: 80);
        }
      }
    }
  }

  &__bottom {
    margin: 50px 0;

    &-details {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 22px;

        &:last-of-type {
          b {
            color: $orange;
          }
        }
      }
    }

    &-buttons {
      display: flex;
      justify-content: space-between;
      margin-top: 40px;

      .go-back-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 210px;

        border-color: darken($color: $gray-line, $amount: 10);

        span {
          color: darken($color: $gray-line, $amount: 20);
          font-weight: 500;
          font-weight: 600;
        }

        &:hover {
          background-color: darken($color: $gray-line, $amount: 90);
          border-color: darken($color: $gray-line, $amount: 90);

          span {
            color: $gray-line;
          }
        }

        svg {
          margin-right: 12px;
          path {
            fill: transparent;
            stroke-width: 2;
          }
        }
      }

      .pay-btn {
        font-size: 16px;
        font-weight: 600;
        width: 210px;
        padding: 16px;
      }
    }
  }

  &--empty {
    margin: 0 auto;
    width: 560px;
    text-align: center;

    h2 {
      font-size: 32px;
      margin-bottom: 10px;
    }

    p {
      font-size: 18px;
      line-height: 145.4%;
      letter-spacing: 0.01em;
      color: #777777;
    }

    icon {
      position: relative;
      top: 2px;
    }

    img {
      display: block;
      width: 300px;
      margin: 45px auto 60px;
    }

    .button--black {
      padding: 12px 0 14px;
      width: 230px;
      margin: 0 auto;
      font-weight: 600;
      font-size: 18px;
    }
  }
}
